const _fixedComponents = [
  /-dialog$/i,
  /-fab$/i,
  /-modal$/i,
  /-popover$/i,
  /-speed-dial$/i,
  /-toolbar$/i
];


/* Public */
export const filterFixedContent = rawContent => {
  return rawContent.reduce((r, e) => {
    let position = 'content';
    if (e.tag) {
      if (_fixedComponents.some(rexp => rexp.test(e.tag))) {
        position = /ons-toolbar$/i.test(e.tag) ? 'toolbars' : 'fixed';
      } else if (e.hasOwnProperty('data') && e.data
          && e.data.hasOwnProperty('staticClass') && e.data.staticClass
          && e.data.staticClass.match(/content/i)
      ) {
        r.providedContent = e;
      }
    }

    r[position].push(e);
    return r;
  }, {
    content: [],
    fixed: [],
    toolbars: []
  });
};

export const hyphenate = string => string.replace(/([a-zA-Z])([A-Z])/g, '$1-$2').toLowerCase();

export const camelize = string => string.toLowerCase().replace(/-([a-z])/g, (m, l) => l.toUpperCase());

export const eventToHandler = name => '_on' + name.charAt(0).toUpperCase() + name.slice(1);

export const handlerToProp = name => name.slice(2).charAt(0).toLowerCase() + name.slice(2).slice(1);




